<template>
    <div>
        <v-progress-linear v-model="progress" :active="showProgress"></v-progress-linear>
        <figure :class="{ removed: stopVideo() }">
            <video autoplay ref="nvisionVideo"
                   class="video-player" @click="snapshot"></video>
        </figure>
        <div :class="{ removed: !stopVideo() }">
            <canvas ref="nvisionCapturedImage" class="video-player"></canvas>
        </div>
        <div class="above-overlay">
            <div class="help-text">center container number in red brackets</div>
            <div class="bounding-box">
                <div class="left-side"/>
                <div class="spacer"/>
                <div class="right-side"/>
            </div>
        </div>

        <v-bottom-sheet inset :value="containerActions" ref="nvisionContainerActions" hide-overlay>
            <v-list>
                <v-list-tile v-if="multipleResults" @click="searchResults(matchedString)">
                    <v-list-tile-title>Search on: {{matchedString}}</v-list-tile-title>
                    <v-btn icon>
                        <v-icon>search</v-icon>
                    </v-btn>
                </v-list-tile>
                <v-list-tile v-if="!multipleResults" @click="viewContainer(matchedContainer.id)">
                    <v-list-tile-title>
                        View Container: <strong>{{matchedContainer.id}}</strong>
                    </v-list-tile-title>
                    <v-btn icon>
                        <v-icon>play_arrow</v-icon>
                    </v-btn>
                </v-list-tile>
            </v-list>
        </v-bottom-sheet>
        <div class="overlay"/>
        <div class="header-items">
            <v-btn icon class="back-button" @click="reset" :class="{hidden : !showBackButton}">
                <v-icon color="white">arrow_back</v-icon>
            </v-btn>
            <v-toolbar
                    dense
                    floating
                    class="search-bar"
            >
                <v-text-field
                        hide-details
                        solo flat
                        prepend-icon="search"
                        single-line
                        placeholder="Find Containers"
                ></v-text-field>
            </v-toolbar>
        </div>
    </div>
</template>

<script>
import containerSvc from '@/services/containerSvc';
import dataService from '@/services/dataService';

export default {
  name: 'Vision',
  components: {},
  mounted() {
    this.setupVideo();
  },
  methods: {
    setupVideo() {
      const constraints = {
        video: {
          facingMode: 'environment',
        },
      };

      navigator.mediaDevices.getUserMedia(constraints)
        .then((stream) => {
          window.stream = stream;
          this.$refs.nvisionVideo.srcObject = stream;
        })
        .catch((err) => {
          console.log(err);
        });
    },
    snapshot() {
      const video = this.$refs.nvisionVideo;
      const canvas = this.$refs.nvisionCapturedImage;

      canvas.width = video.videoWidth;
      canvas.height = video.videoHeight;
      canvas.getContext('2d')
        .drawImage(video, 0, 0);
      this.videoPlaying = false;

      const windowHeight = window.screen.height;
      const windowWidth = window.screen.width;

      const canvasWidth = canvas.width;
      const canvasHeight = canvas.height;

      const newCanvas = document.createElement('canvas');

      // set dimensions
      newCanvas.width = canvas.width;
      newCanvas.height = canvas.height;

      const heightRatio = windowHeight / canvasHeight;
      const widthRatio = windowWidth / canvasWidth;

      // apply the old canvas to the new one
      newCanvas.getContext('2d')
        .drawImage(
          canvas,
          0, 128 / heightRatio, canvas.width, 100 / heightRatio,
          0, 0, canvas.width, 100 / heightRatio,
        );

      this.showProgress = true;
      // eslint-disable-next-line
      Tesseract.recognize(newCanvas)
        .progress((message) => {
          this.progress = message.progress * 100;
        })
        .then((result) => {
          const matchedContainers = containerSvc.getMatchingContainers(result.text, this.containers);
          this.multipleResults = true;
          if (matchedContainers.length > 0) {
            this.matchedString = matchedContainers[0].matchString;
          }
          if (matchedContainers.length === 1) {
            this.multipleResults = false;
            [this.matchedContainer] = matchedContainers;
          } else if (matchedContainers.length > 1) {
            if (matchedContainers[0].matchString.length > matchedContainers[1].matchString.length) {
              this.multipleResults = false;
              [this.matchedContainer] = matchedContainers;
            }
          }
          this.containerActions = true;
          this.showProgress = false;
          this.showBackButton = true;
        });
    },
    stopVideo() {
      return !this.videoPlaying;
    },
    reset() {
      this.videoPlaying = true;
      this.showBackButton = false;
    },
    parseId(text) {
      // add actual parsing logic here!
      return 'TRLU 679647 0';
    },
    viewContainer(id) {
      this.$router.push({
        name: 'container',
        params: { id },
      });
    },
    searchResults(query) {
      this.$router.push({
        path: `search/${query}`,
      });
    },
  },
  firestore() {
    return {
      containers: dataService.getData(),
    };
  },
  data() {
    return {
      progress: 0,
      nvisionCaptureSrc: '',
      videoPlaying: true,
      containerActions: false,
      showProgress: false,
      showBackButton: false,
      matchedContainer: {},
      matchedString: '',
      multipleResults: false,
      containers: [],
    };
  },
};
</script>

