<template>
    <div>
        <div>
            <input class="my-input" type="text"
                   v-model="newItem"
                   @keyup.enter="addItem">
            <button  @click="addItem">
                Add Item
            </button>
        </div>
        <ul class="reptileList">
            <li v-for="item in items" >
                {{ item.name }} -
                <button @click="deleteItem(item)">
                    Remove
                </button>
            </li>
        </ul>
    </div>
</template>

<script>
  import dataService from '../services/dataService';

  export default {
    name: 'MyComponent',
    components: {},
    data() {
      return {
        items: [],
        newItem: ''
      }
    },
    firestore() {
      return {
        items: dataService.getData(),
      }
    },
    methods: {
      addItem: function() {
        dataService.addData(this.newItem);
        this.newItem = '';
      },
      deleteItem: function(item) {
        this.$firestore.items.doc(item['.key']).delete();
      }
    }
  };
</script>

<style scoped>
.my-input {
    background-color: white;
}
</style>
